@import url(https://fonts.googleapis.com/css?family=Just+Another+Hand|Luckiest+Guy&display=swap);
header{width:100%;height:100%;font-family:'Luckiest Guy', sans-serif;background:#232526;background:-webkit-gradient(linear, left top, right top, from(#414345), to(#232526));background:linear-gradient(to right, #414345, #232526);margin:0 auto;padding:1rem 1rem 0;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#fff;border-bottom:3px solid #fff;text-align:center}h1{font-size:3rem;letter-spacing:0.3rem;text-shadow:2px 1px #000;margin:0.5rem 0 0}

form{font-size:1.5rem;margin:1rem 0;letter-spacing:0.1rem;text-shadow:2px 1px #000}label{display:flex;flex-direction:column;align-items:center;justify-content:center}input{width:15rem;font-size:1.2rem;margin:0.5rem 0;padding:0.5rem;border-radius:10px;border:none;box-shadow:0px 0px 3px #fff}.button{width:5rem;cursor:pointer;margin:0.5rem 0;background-color:#ff5202;color:#fff;font-weight:600;box-shadow:0px 0px 3px #b1b1b1}

.App{width:100%;height:auto;display:flex;flex-direction:column;align-items:center}@media screen and (min-width: 600px){.App{width:100%;height:auto;display:flex;flex-direction:column;align-items:inherit}}

article{width:300px;height:300px;margin:0 auto;font-family:'Just Another Hand', sans-serif;position:relative;display:flex;flex-direction:column;align-items:center;justify-content:center}.post-it{position:absolute;width:100%;height:100%;background-image:url(/memory-list/static/media/postit.ab9fb559.png);background-size:cover;background-repeat:no-repeat;opacity:1}.post-it-2{position:absolute;width:100%;height:100%;background-image:url(/memory-list/static/media/postit2.80251847.png);background-size:cover;background-repeat:no-repeat;opacity:0}.note{width:70%;font-size:3rem;position:absolute;margin:0 auto;line-height:1.5;-webkit-transform:rotate(-5deg);transform:rotate(-5deg)}.btn{font-size:1.5rem;background-color:transparent;border:none;cursor:pointer;position:absolute;z-index:5}.check{opacity:1;bottom:1rem;right:4rem;color:green}.cross{opacity:0;bottom:1rem;right:4rem;color:#000}.trash{bottom:1rem;right:2rem;color:red}.is-done{font-size:2rem;background-color:transparent;border:none;cursor:pointer;position:absolute;z-index:5;opacity:1;top:2rem;right:2rem}

.container{display:grid;grid-template-columns:repeat(auto-fit, minmax(290px, 1fr));padding:1rem}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-image: url(/memory-list/static/media/board.d3c0c152.jpg);
    background-position: top;
}
